* {
  margin: 0;
  padding: 0;
}
.perfil {
  display: flex;
  height: auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 20px 0 40px;
  color: #fafafa;
  padding-bottom: 30px;
  border-bottom: 1px #fafafa solid;
}
.image {
  width: 150px;
  height: 150px;
  border: 1px #111 solid;
  border-radius: 50%;
  box-sizing: border-box;
  overflow: hidden;
}
.image img{
  width: 150px;
  
}
.nombre {
  font-size: 30px;
  color: #ef2d56;
  /*color:#ff1053;*/
}
