* {
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
  flex-direction: column;
  width: 80%;

  border-radius: 1rem;
  background-color: #292824;
}
tr {
  height: 5rem;
  margin: 1rem;
}
caption {
  font-size: 2rem;
  margin: 1rem;
  padding: 1rem;
  border-bottom: 1px #fafafa solid;
}
th {
  font-size: 1.5rem;
}
td button {
  height: 2rem;
  width: 5rem;
  border-radius: 1rem;
  background-color: #ef2d56;
  color: #fafafa;
  cursor: pointer;
  border: none;
}
@media (max-width:900px){
  caption{
    font-size: 1.5rem;
  }
  th{
    font-size: 10px;
  }
  td{
    font-size: 10px;
  }
  td button{
    width: 3.5rem;
    font-size: 10px;
  }
}