* {
  margin: 0;
  padding: 0;
}
.infoContent {
  background-color: #677db7;
  color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 300px;
  top: 0;
  right: 0;
  height: 100vh;
  border: 1px #111 solid;
  box-sizing: border-box;
  overflow: hidden;
  padding-top: 50px;
}
.infoContentFull {
  background-color: #677db7;
  color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 100vh;
  border: 1px #111 solid;
  box-sizing: border-box;
  transition: 1s;
  padding-top: 50px;
}
