* {
  margin: 0;
  padding: 0;
}
.modal {
  position: absolute;
  top: 2rem;
  margin: auto 0;
  width: 80%;
  height: 90%;
  z-index: 100000;
  border: 1px #292824 solid;
  box-sizing: border-box;
  background-color: #292824;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalImage {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 70px;
  top: 70px;
  width: 100%;
  background-color: #292824;
  box-sizing: border-box;
}
.modalImage img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}
