*{
    margin: 0;
    padding: 0;
}
.cuadro{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 80%;
    border:1px #292824 solid;
    border-radius: 1rem;
    background-color: #292824;
    font-size: 2rem;
}
.cuadro h2{
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px #fafafa solid;
    font-size: 2rem;
}
.datos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
.datos h3{
    margin-bottom: 1rem;
}
.dato{
    opacity: 0.5;
}
@media (max-width:900px){
    .cuadro h2{
        font-size: 1.5rem;
    }
    .datos{
        font-size: 1rem;
    }
}