* {
  margin: 0;
  padding: 0;
}
.Sidebar {
  background-color: #292824;
  display: flex;
  flex-direction: column;
  padding: 30px 10px;
  width: 300px;
  height: 100vh;
  border: 1px #292824 solid;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
}
.link {
  text-decoration: none;
  margin-bottom: 20px;
  color: #fafafa;
  font-size: 30px;
  font-weight: 600;
  display: block;
  border-radius: 15px;
  transition: 0.5s;
}
.link:hover {
  color: #677db7;
  transform: translateX(10px);
  transition: 0.5s;
}
