* {
  margin: 0;
  padding: 0;
}
.homeContent {
  background-color: #677db7;
  color: #fafafa;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  position: absolute;
  left: 300px;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  border: 1px #111 solid;
  box-sizing: border-box;
  transition: 0.5s;
  overflow-y: scroll;
  padding-top: 50px;
  
}
.homeContentFull {
  background-color: #677db7;
  color: #fafafa;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  border: 1px #111 solid;
  box-sizing: border-box;
  transition: 1s;
  overflow-y: scroll;
  padding-top: 50px;
}
.title{
  font-size: 100px;
  padding-top: 1rem;
}
.name{
  color: #ef2d56;
}
.subtitle{
  font-size: 70px;
}
.intro{
  width: 80%;
  font-family: 'Courier New', Courier, monospace;
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 30px;
  font-weight: 500;
}
.tecs{
  font-size: 40px;
  font-weight: 800;
}
.highlight{
  color: #ef2d56;
  font-weight: 600;
}
@media (max-width:500px){
  .title{
    font-size: 80px;
  }
  .subtitle{
    font-size: 56px;
  }
}