* {
  margin: 0;
  padding: 0;
}
.Card {
  background-color: #292824;
  width: 300px;
  height: 600px;
  border: 1px grey solid;
  border-radius: 20px;
  margin: 10px 0;
}
.image {
  background-color: #f3f3f3;
  height: 300px;
  width: 300px;
  border-radius: 20px 20px 0 0;
}
.image img {
  border-radius: 20px 20px 0 0;
}
.info {
  color: #f3f3f3;
  width: 300px;
  margin: 10px auto;
  padding: 0 px;
}
.info .title {
  border-bottom: 1px #f3f3f3 solid;
  margin: 20px;
}
.info .details h4 {
  opacity: 50%;
  margin-bottom: 5px;
}
.info .details h3 {
  margin-bottom: 15px;
}
