* {
  margin: 0;
  padding: 0;
}
.btnclose {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  position: absolute;
  right: 15px;
  top: 15px;
  background-color: #292824;
  border: none;
}
.cross {
  width: 25px;
  height: 25px;
  color: #fafafa;
}
.btnclose:hover {
  cursor: pointer;
}
