*{
    margin: 0;
    padding: 0;
}
.bubble{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 5px #292824 solid;
    overflow: hidden;
    background-color: white;
    justify-content: center;
    align-items: center;
}
.bubble img{
    padding-top: 15px;
    height: 120px;

}